import ParseCtrl from "./parse-ctrl.js";

export default function parse (text) {
	const ctrl = new ParseCtrl(text);
	const parse = {
		string () {
			if (ctrl.node.type === "string") {
				if (ctrl.match(ctrl.node.data.quote)) {
					ctrl.go().end();
					return true;
				}
			}
			if (ctrl.match(/^["'`]/)) {
				ctrl.start();
				return true;
			}
		},
	};

	while (!ctrl.isEnd()) {
		switch (ctrl.node.type) {
			case "string": {
				parse.string();
				break;
			}
			default: {
				[
					parse.string,
				].some(p => p());
				break;
			}
		}
		ctrl.next();
	}
	return ctrl;
}
